html, body {
    height: 100%;
}

#root {
    height: 100%;
}

.App {
    display: flex;
    flex-direction: column;
    height: 100%;
    text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.jumbotron {
    display: flex;
    height: 100%;
    flex-direction: column;
    flex: 1 1 auto;
    margin-bottom: 0;
    padding: 0;
    border-radius: 0;
    background-color: #393e44;
}

.main-content {
  margin: auto 0;
  color: #fff;
  .content {
    font-size: 24px;
  }
}

.main-canvas {
  width: 100%;
  height: 100%;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
